body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  background-color: #a5a2a2;
  height: 100vh;
}
body.fijar-body {
  overflow: hidden;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

main {
  background-image: linear-gradient(to top, #352ed3, #382ed0, #3b2ecc, #3d2ec9, #3f2ec6, #412ec7, #422ec9, #442eca, #462ed0, #482ed5, #4b2edb, #4d2ee1);
  background-repeat: no-repeat;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Open Sans", sans-serif;
}

h2 {
  font-size: 4rem;
  font-family: "Roboto", cursive;
}

h3 {
  font-size: 3rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #FFFFFF;
  font-size: 1.6rem;
  line-height: 1.8;
  font-family: "Open Sans", sans-serif;
}

.chat__main {
  display: flex;
  justify-content: center;
}
.chat__main .chat__contenedor {
  margin-top: 7rem;
  flex-basis: 100%;
  border-radius: 1rem;
  background-color: #01081f;
  box-shadow: -3px 8px 5px 0px rgba(0, 0, 0, 0.41), -2px 3px 5px rgba(71, 70, 70, 0.267);
}
@media (min-width: 768px) {
  .chat__main .chat__contenedor {
    flex-basis: 50%;
  }
}
.chat__main .chat__header {
  display: flex;
  align-items: center;
  padding: 1rem;
  padding-left: 8rem;
  gap: 2rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.315);
}
.chat__main .chat__header h2 {
  font-size: 1.8rem;
  color: #FFFFFF;
}
.chat__main .chat__header .chat__img img {
  border-radius: 50%;
  width: 50px;
}
.chat__main .chat {
  padding: 2rem;
  height: 400px;
  overflow: hidden;
  overflow-y: auto;
  scrollbar-gutter: stable;
}
.chat__main .chat .chat__mensaje {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 1.2rem 0;
}
.chat__main .chat .chat__icon img {
  width: 50px;
  border-radius: 50%;
}
.chat__main .chat .chat__info {
  position: relative;
  background-color: #FFFFFF;
  padding: 0.8rem;
  border-radius: 0.5rem;
  box-shadow: inset -14px 2px 15px 3px rgba(0, 0, 0, 0.33);
}
.chat__main .chat .chat__info::after {
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  top: 35%;
  left: -4px;
  transform: rotate(46deg);
  display: block;
  background-color: #FFFFFF;
  z-index: 0;
}
.chat__main .chat .chat__info p {
  position: relative;
  color: #01081f;
  margin: 0;
  font-size: 1.3rem;
  z-index: 2;
}
.chat__main .chat::-webkit-scrollbar {
  width: 10px;
}
.chat__main .chat::-webkit-scrollbar-track {
  background-color: #412EC2;
  border-top-left-radius: 0;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  overflow: hiden;
}
.chat__main .chat::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
}
.chat__main .chat .chat__lista li:nth-child(even) .chat__mensaje {
  flex-direction: row-reverse;
}
.chat__main .chat .chat__lista li:nth-child(even) .chat__mensaje .chat__info {
  box-shadow: inset 14px 2px 15px 3px rgba(0, 0, 0, 0.33);
}
.chat__main .chat .chat__lista li:nth-child(even) .chat__mensaje .chat__info::after {
  left: unset;
  right: -4px;
}
.chat__main .chat__footer {
  padding: 1rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  background-color: #FFFFFF;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}
.chat__main .chat__footer input {
  appearance: none;
  border: none;
}
.chat__main .chat__footer span {
  cursor: pointer;
}